*{
  
  font-family:"Rubik", sans-serif !important; 

}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


:root {
  --background-color: #537571;
}
/* GLOBAL RULES*/
* {
  padding: 0;
  margin: 0;
  font-family: "Fredoka", sans-serif;
  font-family: "Rubik", sans-serif;
}

/* HOME SECTION*/
.home {
  background-image: linear-gradient(
      45deg,
      rgba(32, 32, 32, 0.62),
      rgba(8, 8, 8, 0.58)
    ),
    url("https://raw.githubusercontent.com/uditrsahani/Airbnb-Clone-Frontend/master/img/bg-banner.png");
  background-position: center;
  background-size: contain;
  height: 100vh;
}

/* HOMEPAGE FLEXBOX*/
.home_flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 20%;
  color: white;
  box-sizing: border-box;

  height: 100vh;
}

/* ABOUT SECTION */
.about_section {
  /* height: 100vh; */
}

/* ABOUT FLEX */
.about_flex {
  /* height: 100vh; */

  display: flex;
  padding: 50px;
  box-sizing: border-box;
}

/* WORK SECTION */
.work {
  /* height: 100vh; */
}

/* WORK FLEX */
.work_flex {
  display: flex;
  /* height: 100vh; */

  box-sizing: border-box;
  /* justify-content: space-around; */
  /* justify-content: center; */
}

/* CONTACT SECTION */
.contact {
  height: 100vh;
}


/* UTITLITY */
/* PROFILE PICTURE ON THE HOMEPAGE*/
.home_flex > img {
  height: 150px;
  width: 150px;
}

/* UDIT SAHANI ON THE HOMEPAGE*/
.home_flex > .name {
  font-weight: bold;
  font-size: 40px;
}

/* HORIZONTAL ROW ON THE HOEPAGE*/
hr {
  color: white;
  width: 13vw;
  margin-bottom: 20px;
}

/* SIAM UNIVERSITY TITLE ON HOMEPAGE*/
.siam_university {
  letter-spacing: 3px;
  font-size: 20px;
  margin-bottom: 20px;
}

/* SOCIAL ICON ON THE HOMEPAGE*/
.social_icon > a {
  padding: 0 30px;
  color: white;
}

/* Contact Me Button */
.contactBtn {
  margin-top: 20px;
  padding: 10px 50px;
  border-radius: 50px;
  background-color: #00695b;
  color: white;
  font-family: "Rubik", sans-serif;
  letter-spacing: 3px;
  border-style: none;
}

.about_text > a{
  text-decoration: none;
}

/* CONTACT BUTTON LINK */
.contactBtn > a {
  color: white;
  text-decoration: none;
  font-family: "Rubik", sans-serif;
  font-weight: 1200;
}

/* PROFILE PICTURE IN ABOUT SECTION  */
.udit_img_about > img {
  height: 150px;
}

/* ABOUT TEXT IN THE ABOUT SECTION */
.about_text {
  padding: 0 50px;
}

/* DOWNLOAD CV BUTTON IN THE ABOUT SECTION */
.download_cv {
  background-color: black;
  color: white;
  padding: 10px 30px;
  border-radius: 20px;
  letter-spacing: 3px;
  font-weight: bold;
  border-style: none;
}

/* PROGRESSBAR IN THE ABOUT SECTION */
/* For Firefox */
#html::-moz-progress-bar {
  background: #f3366f;
}

/* For Chrome or Safari */
#html::-webkit-progress-value {
  background: #e9346a;
}

/* For Firefox */
#JS::-moz-progress-bar {
  background: #000000;
}

/* For Chrome or Safari */
#JS::-webkit-progress-value {
  background: #000000;
}

/* For Firefox */
#Node::-moz-progress-bar {
  background: rgb(235, 235, 12);
}

/* For Chrome or Safari */
#Node::-webkit-progress-value {
  background: rgb(235, 235, 12);
}

/* For Firefox */
#PostgreSQL::-moz-progress-bar {
  background: rgb(33, 185, 33);
}

/* For Chrome or Safari */
#PostgreSQL::-webkit-progress-value {
  background: rgb(28, 209, 28);
}

progress {
  border-radius: 10px;
  width: 25vw;
  height: 10px;
}

/* IMAGES IN THE MY WORK SECTION */

.project > img {
  height: 280px;
  width: 500px;
  margin: 50px 20px 10px 50px;
}

.break_line{
  height: 0;
  flex-basis: 100%;
}

/* ADD BORDER TO THE PROJECT IMAGES */


/* PREVIEW BUTTON IN THE MY WORK SECTION
.preview{
  position: absolute 
} */

.previewButton{
  padding: 10px 15px;
  background-color: black;
  color: white;
  border-style: none;
  border-radius: 10px;
 
}




/* FORM IN CONTACT SECTION */
form {
  padding: 50px;
}

/* FIRST NAME AND LAST NAME */
#fname,
#lname {
  width: 35%;
  padding: 10px 0;
  border-radius: 30px;
  border: solid 1px grey;
  margin-top: 5px;
}

/* FIRST NAME MARGIN */
#fname {
  margin: 0 9.5% 0 0;
}

/* SUBJECT, EMAIL and MESSAGE width, padding and border-radius */
#subject,
#email,
#message {
  width: 80%;
  padding: 10px 0;
  border-radius: 30px;
  border: solid 1px grey;
  margin-top: 5px;
}

/* MESSAGE BOX */
#message {
  padding: 50px 0;
  border-radius: 30px;
  margin-top: 5px;
}

/* SEND BUTTON In THE CONTACT SECTION */
.submit_btn {
  color: white;
  background-color: black;
  padding: 11px 30px;
  border-radius: 20px;
  font-weight: bold;
  letter-spacing: 2px;
  border-style: none;
  margin-bottom: 50px !important;
}

/* FOOTER TEXT */
.footer {
  background-color: #00695b;
  color: white;
  padding: 10px 0;
}


/* MEDIA CSS */
@media screen and (max-width: 600px) {
  .about_flex, .work_flex{
    flex-direction: column;
    align-items: center;
  }

  .about_flex > div{
    padding: 10px 0;
  }

  progress {
    width: 80vw;
  }

  .project > img {
   
    width: 80vw;
    margin-bottom: 5px;
    margin-top: 10px;
  }

  #project2_image{
    padding-top: 0;
  }

  #project1_image{
    padding-bottom: 15px;
  }

}

@media screen and (max-width: 600px){
  .home{
    height: 90vh;
  }

  .siam_university_media{
    font-size: 16px;
  }

  #name_media{
    font-size: 30px;
    margin-top: 10px;
  }

  hr{
    width: 20vw;
  }

  .download_cv{
    margin-bottom: 10px;
  }

  .my_work_title{
    margin-bottom: 20px;
  }
}

/* GLOBAL CSS */

/* TITLE and IT's Background Box */
.title {
  padding-left: 50px;
  z-index: 10;
}

.title_backgroud {
  background-color: var(--background-color);
  width: 55px;
  border-radius: 5px;
  height: 25px;
  margin-left: 50px;
  position: relative;
  top: 4%;
  z-index: 0;
}

.text_center {
  text-align: center;
}

